body{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(to bottom, black, darkblue );
    height: 100vh;
}
.container{
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 3px solid darkslategrey;
    border-radius: 5px;
    width: 35vw;
    height: 90vh;
    padding: 5px;
    background: linear-gradient(to top, black, #282828);
}
.light{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: 5px 10px;
    border: 2px solid black;
    border-top: 10px solid #181818;
    padding-top: 2px;
}
.red-light{
    background-color: rgb(248, 16, 16);
}
.yellow-light{
    background-color: rgb(255, 255, 9);
}
.green-light{
    background-color: rgb(18, 150, 18);
}
.highlight-red{
    box-shadow: 0 0 70px red;
}
.highlight-yellow{
    box-shadow: 0 0 70px yellow;
}
.highlight-green{
    box-shadow: 0 0 70px rgb(93, 241, 93);
}
button{
    border-radius: 2px;
    outline: 1px solid darkslategray;
    margin-top: 10px;
    background-color: gray;
    color: snow;
    font-weight: bold;
    padding: 3px;
}
.timer{
    background-color: black;
    color: snow;
    font-size: 1.5em;
    width: 100%;
    text-align: center;
    font-weight: bolder;
}